import React, {useState} from 'react';
import './ExploreContainer.css';
import {
    IonCard,
    IonCardContent,
    IonCardHeader,
    IonCardTitle,
    IonCol,
    IonItem,
    IonLabel,
    IonProgressBar,
    IonToggle
} from "@ionic/react";

const Buffer = require('buffer').Buffer;

const crypto = require('crypto');

// @ts-ignore
const colours = require.context("../../downloads", true, /\.(colour.json)$/);
const colourFiles = colours.keys();
// @ts-ignore
const nocolours = require.context("../../downloads", true, /\.(nocolour.json)$/);
const nocolourFiles = nocolours.keys();

interface ContainerProps {
}

const toBase64 = (file: File) => new Promise((resolve, reject) => {
    const reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = () => resolve(reader.result);
    reader.onerror = error => reject(error);
});

const toBuffer = (file: File) => new Promise((resolve, reject) => {
    const reader = new FileReader();
    reader.readAsArrayBuffer(file);
    reader.onload = () => resolve(reader.result);
    reader.onerror = error => reject(error);
});

const ExploreContainer: React.FC<ContainerProps> = () => {
    const [results, setResult] = useState({});
    const [colour, setColour] = useState(true);
    const [checkedFiles, setFiles] = useState(0);
    const [allFiles, setAllFiles] = useState(0);

    const toggleColour = () => {
        setColour(!colour);
    };

    const onChangeHandler = async (event: any) => {
        event.persist();
        let uploadedFiles: any = [];
        for (let i = 0; i < event.target.files.length; i++) {
            let file = event.target.files[i];
            if (file.name.includes(".png")) {
                // @ts-ignore
                let array = new Uint8Array(await toBuffer(file));
                for (let i = 0; i < array.length; i++) {
                    if (array[i] !== 0) {
                        array[i] = 255;
                    }
                }

                let details: any = {
                    base: await toBase64(file),
                    nocolour: Buffer.from(array).toString('base64'),
                    name: file.name
                };

                details.hash = crypto.createHash('md5')
                    .update(details.base.replace("data:image/png;base64,", ""))
                    .digest('hex');

                details.nocolourhash = crypto.createHash('md5')
                    .update(details.nocolour)
                    .digest('hex');

                uploadedFiles.push(details);
            }
        }
        findMatches(uploadedFiles);
    };

    const findMatches = (uploadedFiles: any[]) => {
        let found: any = [];

        let checkingFiles = nocolourFiles;
        let checkingData = nocolours;
        if (colour) {
            checkingFiles = colourFiles;
            checkingData = colours;
        }

        setAllFiles(checkingFiles.length);
        for (let j = 0; j < checkingFiles.length; j++) {
            setFiles(j);
            let checking = checkingData(checkingFiles[j]).hash;
            if (checking === null) {
                continue;
            }

            for (let i = 0; i < uploadedFiles.length; i++) {
                let comparisonImage = uploadedFiles[i];

                if (found[i]) {
                    continue;
                }

                let compareHash;

                if (colours) {
                    compareHash = comparisonImage.hash;
                } else {
                    compareHash = comparisonImage.nocolourhash;
                }

                if (compareHash === checking) {
                    found[i] = true;
                    let strings = checkingFiles[j].split('/');
                    strings[strings.length - 1] = "Attribution.txt";
                    let strings2 = checkingFiles[j].split('/');
                    strings2[strings2.length - 1] = "opengameart.json";

                    let resultTemp: any = results;
                    // @ts-ignore
                    resultTemp[uploadedFiles[i].name] = {
                        file: checkingFiles[j],
                        attribution: "No attribution could be found",
                        src: comparisonImage.base,
                        name: uploadedFiles[i].name
                    };
                    setResult({...resultTemp});

                    try {
                        fetch("https://raw.githubusercontent.com/ElectricBrainUK/PNGCompare/master/downloads/" + strings.join('/').replace("./", "")).then((res) => {
                            let resultTemp: any = results;
                            resultTemp[uploadedFiles[i].name].attribution = strings.join('/');
                            setResult({...resultTemp});
                        }).catch(err => {
                            console.log(err);
                        });
                    } catch (e) {
                        console.log(e);
                    }

                    try {
                        fetch("https://raw.githubusercontent.com/ElectricBrainUK/PNGCompare/master/downloads/" + strings2.join('/').replace("./", "")).then((openURL) => {
                            openURL.json().then(json => {
                                let resultTemp: any = results;
                                resultTemp[uploadedFiles[i].name].url = json.url;
                                resultTemp[uploadedFiles[i].name].username = json.username;
                                resultTemp[uploadedFiles[i].name].ogName = json.name;
                                setResult({...resultTemp});
                            });
                        }).catch(err => {
                            console.log(err);
                        });
                    } catch (e) {
                        console.log(e);
                    }
                }
            }
        }

        for (let i = 0; i < uploadedFiles.length; i++) {
            // @ts-ignore
            if (!results[uploadedFiles[i]] && !found[i]) {
                let resultTemp: any = results;
                resultTemp[uploadedFiles[i].name] = {
                    attribution: "No attribution could be found",
                    src: uploadedFiles[i].base,
                    name: uploadedFiles[i].name
                };

                setResult({...resultTemp});
            }
        }
    };

    let cards = Object.keys(results).map(key => {
        // @ts-ignore
        let result = results[key];
        return <IonCard key={result.name} style={{maxWidth: "300px", minWidth: "300px", display: "inline-block"}}>
            <img src={result.src} alt={""}/>
            <IonCardHeader>
                <IonCardTitle>{result.name}</IonCardTitle>
                <IonCardTitle>{result.username}</IonCardTitle>
                <IonCardTitle>{result.ogName}</IonCardTitle>
            </IonCardHeader>
            <IonCardContent>
                <IonCol>
                    {
                        result.attribution && result.attribution !== "No attribution could be found" ?
                            <a rel="noopener noreferrer" target="_blank"
                               href={"https://raw.githubusercontent.com/ElectricBrainUK/PNGCompare/master/downloads/" + result.attribution.replace("./", "")}>
                                Attribution
                            </a>
                            :
                            <p>No attribution could be found</p>
                    }
                </IonCol>
                <IonCol>
                    {
                        result.url ?
                            <a rel="noopener noreferrer" target="_blank" href={result.url}>Link</a> : <></>
                    }
                </IonCol>
            </IonCardContent>
        </IonCard>
    });

    return (
        <div id="container">
            <IonItem>
                <IonLabel>Compare Colour</IonLabel>
                <IonToggle checked={colour} onIonChange={toggleColour}/>
            </IonItem>
            <IonProgressBar value={checkedFiles / allFiles}></IonProgressBar>
            <input accept="image/x-png" type="file" name="file" onChange={onChangeHandler} multiple/>
            {
                cards
            }
        </div>
    );
};

export default ExploreContainer;
